import initialState from './initialState';
import * as consts from './consts';

export default function(state = initialState, action) {
    switch (action.type) {
        case consts.GET_SAME_POSTS_REQUEST:
            return state
                .setIn(['samePosts', 'waiting'], true)
                .setIn(['samePosts', 'error'], false);
        case consts.GET_SAME_POSTS_SUCCESS:
            return state
                .setIn(['samePosts', 'waiting'], false)
                .setIn(['samePosts', 'error'], false)
                .setIn(['samePosts', 'data'], action.samePosts);
        case consts.GET_SAME_POSTS_ERROR:
            return state
                .setIn(['samePosts', 'waiting'], false)
                .setIn(['samePosts', 'error'], action.error)
                .setIn(['samePosts', 'data'], false);
        case consts.GET_LATEST_POSTS_REQUEST:
            return state
                .setIn(['latestPosts', 'waiting'], true)
                .setIn(['latestPosts', 'error'], false);
        case consts.GET_LATEST_POSTS_SUCCESS:
            return state
                .setIn(['latestPosts', 'waiting'], false)
                .setIn(['latestPosts', 'error'], false)
                .setIn(['latestPosts', 'data'], action.latestPosts);
        case consts.GET_LATEST_POSTS_ERROR:
            return state
                .setIn(['latestPosts', 'waiting'], false)
                .setIn(['latestPosts', 'error'], action.error)
                .setIn(['latestPosts', 'data'], false);
        case consts.GET_SELECTED_POST_REQUEST:
            return state
                .setIn(['post', 'waiting'], true)
                .setIn(['post', 'error'], false)
                .set('selectedPost', false);
        case consts.GET_SELECTED_POST_SUCCESS:
            return state
                .setIn(['post', 'waiting'], false)
                .setIn(['post', 'error'], false)
                .setIn(['post', 'data'], action.post)
                .set('selectedPost', action.post);
        case consts.GET_SELECTED_POST_ERROR:
            return state
                .setIn(['post', 'waiting'], false)
                .setIn(['post', 'error'], action.error)
                .setIn(['post', 'data'], false)
                .set('selectedPost', false);
        case consts.RESET_SELECTED_POST:
            return state.setIn(['post', 'data'], false);
        default:
            return state;
    }
}
