import { put, call, all, takeLatest } from 'redux-saga/effects';
import services from '../../services/bloggerAPIs';
import * as consts from './consts';
import utils from '../../utils';

function* getSamePostsSaga({ payload }) {
    try {
        const samePosts = yield call(services.getPosts, payload);
        const postsIncludedImages = utils.getImages(samePosts.data.items);
        const postsIncludedPrice = utils.getPrice(postsIncludedImages);
        const postsIncludedCurrency = utils.getCurrency(postsIncludedPrice);
        const postsIncludedProductInfo = utils.getProductInfo(
            postsIncludedCurrency
        );
        yield all([
            put({
                type: consts.GET_SAME_POSTS_SUCCESS,
                samePosts: postsIncludedProductInfo
            })
        ]);
    } catch (error) {
        yield put({
            type: consts.GET_SAME_POSTS_ERROR,
            error: error.response.data
        });
    }
}

function* getLatestPostsSaga() {
    try {
        const latestPosts = yield call(services.getLatestPost);
        const postsIncludedImages = utils.getImages(latestPosts.data.items);
        const postsIncludedPrice = utils.getPrice(postsIncludedImages);
        const postsIncludedCurrency = utils.getCurrency(postsIncludedPrice);
        const postsIncludedProductInfo = utils.getProductInfo(
            postsIncludedCurrency
        );
        yield all([
            put({
                type: consts.GET_LATEST_POSTS_SUCCESS,
                latestPosts: postsIncludedProductInfo
            })
        ]);
    } catch (error) {
        yield put({
            type: consts.GET_LATEST_POSTS_ERROR,
            error: error.response.data
        });
    }
}

export function* getSelectedPostSaga({ payload }) {
    try {
        const post = yield call(services.getSelectedPost, payload);
        const postsIncludedImages = utils.getImages([post.data]);
        const postsIncludedPrice = utils.getPrice(postsIncludedImages);
        const postsIncludedCurrency = utils.getCurrency(postsIncludedPrice);
        const postsIncludedProductInfo = utils.getProductInfo(
            postsIncludedCurrency
        );
        yield all([
            put({
                type: consts.GET_SELECTED_POST_SUCCESS,
                post: postsIncludedProductInfo[0]
            })
        ]);
    } catch (error) {
        yield put({
            type: consts.GET_SELECTED_POST_ERROR,
            error: error.response.data
        });
    }
}

export default function* root() {
    yield takeLatest(consts.GET_SAME_POSTS_REQUEST, getSamePostsSaga);
    yield takeLatest(consts.GET_SELECTED_POST_REQUEST, getSelectedPostSaga);
    yield takeLatest(consts.GET_LATEST_POSTS_REQUEST, getLatestPostsSaga);
}
