import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import cloneDeep from 'lodash.clonedeep';
import { compose } from 'redux';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { createStructuredSelector } from 'reselect';

import Utils from '../../utils';
import { makeSelectLatestPosts } from './selectors';

import LoadingComponent from '../../components/commons/LoadingComponent';

import { getLatestPosts } from './actions';

import injectReducer from '../../utils/injectReducer';
import injectSaga from '../../utils/injectSaga';
import reducer from './reducer';
import saga from './saga';

class LatestPosts extends Component {
    componentDidMount() {
        const { getLatestPosts } = this.props;
        getLatestPosts();
    }

    onHandleSelectPost = post => {
        const { onHandleSelectPost } = this.props;
        onHandleSelectPost(post);
    };

    render() {
        const { latestPosts, selectedPost } = this.props;
        let latestPostsFiltered = [];

        if (latestPosts.waiting) {
            return (
                <Grid container spacing={32} className="latest-post">
                    <LoadingComponent />
                </Grid>
            );
        }

        if (latestPosts.data) {
            if (selectedPost) {
                const latestPostsCloned = cloneDeep(latestPosts.data);
                latestPostsFiltered = latestPostsCloned.filter(
                    post => post.id !== selectedPost.id
                );

                if (latestPostsFiltered.length > 10) {
                    latestPostsFiltered = latestPostsFiltered.pop();
                }
            } else {
                latestPostsFiltered = latestPosts.data;
            }
        }

        if (latestPostsFiltered.length <= 0) {
            return null;
        }

        return (
            <div className="latest-post">
                {latestPostsFiltered.map(post => {
                    const postUrl = Utils.convertStringToURL(post.title);
                    const uri =
                        '/' +
                        post.labels[0] +
                        '/' +
                        postUrl +
                        '-idpx' +
                        post.id +
                        '.html';
                    return (
                        <div
                            key={post.id}
                            className="col-l-12 col-12 col-s-6 post-item"
                        >
                            <Link
                                to={uri}
                                onClick={() => this.onHandleSelectPost(post)}
                            >
                                <div className="img">
                                    <img
                                        src={post.images[0]}
                                        alt={post.title}
                                    />
                                </div>
                                <Typography
                                    className="card-title"
                                    gutterBottom
                                    variant="h5"
                                    component="h4"
                                >
                                    {post.title}
                                </Typography>
                                {post.price && (
                                    <div className="price">
                                        {post.price}
                                        {post.currency && (
                                            <span className="currency">
                                                {post.currency}
                                            </span>
                                        )}
                                    </div>
                                )}
                            </Link>
                        </div>
                    );
                })}
            </div>
        );
    }
}

LatestPosts.propTypes = {
    latestPosts: PropTypes.object.isRequired,
    onHandleSelectPost: PropTypes.func.isRequired,
    getLatestPosts: PropTypes.func.isRequired,
    selectedPost: PropTypes.oneOfType([PropTypes.bool, PropTypes.object])
        .isRequired
};

const mapStateToProps = createStructuredSelector({
    latestPosts: makeSelectLatestPosts()
});

const mapDispatchToProps = {
    getLatestPosts
};

const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
);

const withReducer = injectReducer({ key: 'detailReducer', reducer });
const withSaga = injectSaga({ key: 'detailSaga', saga });

export default compose(
    withReducer,
    withSaga,
    withConnect
)(LatestPosts);
