import { createSelector } from 'reselect';
import initialState from './initialState';

const detailSelector = state => state.get('detailReducer', initialState);

const makeSelectSamePosts = () =>
    createSelector(
        detailSelector,
        state => state.get('samePosts').toJS()
    );

const makeSelectPost = () =>
    createSelector(
        detailSelector,
        state => state.get('post').toJS()
    );

const makeSelectLatestPosts = () =>
    createSelector(
        detailSelector,
        state => state.get('latestPosts').toJS()
    );

const makeSelectSelectedPost = () =>
    createSelector(
        detailSelector,
        state => state.get('selectedPost')
    );

export {
    makeSelectSamePosts,
    makeSelectPost,
    makeSelectLatestPosts,
    makeSelectSelectedPost
};
